html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  background-color: #F3F3F3;
}

p {
  color: #151515;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #151515;
}

img {
  display: block;
  height: auto;
  width: 100%;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container-sm {
  max-width: 70rem;
  width: 90%;
  margin: 0 auto;
}

body {
  font-family: "Nunito Sans", sans-serif;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

p {
  font-family: "Nunito Sans", sans-serif;
}

.form {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-top: 2rem;
  padding: 3rem;
  border-radius: 1.25rem;
  background-color: #FFFFFF;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  color: #151515;
  overflow: hidden;
}
.form::before {
  content: "";
  position: absolute;
  top: -88rem;
  left: 50%;
  translate: -50% 0;
  width: 100rem;
  height: 100rem;
  border-radius: 50%;
  background-color: #0174F6;
}
@media (min-width: 768px) {
  .form::before {
    width: 150rem;
  }
}
.form__icon {
  z-index: 10;
  margin: 0 auto;
}
.form__legend {
  margin-top: 5rem;
  margin-bottom: 1.5rem;
  text-align: center;
  text-transform: capitalize;
  font-family: "Montserrat", sans-serif;
  font-size: clamp(3rem, -0.8rem + 10vw, 4rem);
}
.form__field {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.form__label {
  margin-left: 1rem;
  font-size: clamp(1.7rem, -0.8rem + 10vw, 2rem);
}
.form__input, .form__textarea {
  background: transparent;
  border: 0.2rem solid #e6e6e6;
  padding: 1.5rem;
  color: #151515;
  transition: border-color 200ms;
  resize: none;
}
.form__input:focus, .form__textarea:focus {
  border-color: #0174F6;
  outline: none;
}
.form__input::placeholder, .form__textarea::placeholder {
  color: #8A8A8A;
}
.form__input {
  border-radius: 2.8rem;
}
.form__textarea {
  border-radius: 1.5rem;
}
.form__error {
  line-height: 0;
  margin-left: 1rem;
  font-size: 1.7rem;
  font-weight: 700;
  color: #F48080;
}
.form__submit {
  background-color: #0174F6;
  color: #FFFFFF;
  text-align: center;
  border: none;
  border-radius: 2.4rem;
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms;
  margin-top: 2rem;
}
.form__submit:hover {
  cursor: pointer;
  color: #FFFFFF;
  background-color: #00A7DE;
}
@media (min-width: 768px) {
  .form__submit {
    width: 40rem;
    margin: 0 auto;
  }
}

.swal2-popup {
  zoom: 150%;
}
.swal2-html-container {
  zoom: 1.3;
}